import { Injectable } from "@angular/core";
import { action, observable } from "mobx";

interface TeacherState {
    projects: string[];
    classes: string[];
}

@Injectable()
export class TeacherStore {
    @observable
    state!: TeacherState;

    constructor() {
        this.initState();
    }

    @action
    initState(): void {
        this.state = {
            projects: [
                "QForm литье",
                "QForm штамповка",
                "QForm основы"
            ],
            classes: [
                "Группа 641",
                "Группа 642",
                "Группа 643"
            ]
        };
    }
}