import { Component } from "@angular/core";
import { TeacherStore } from "./teacher.store";
import { ActivatedRoute, Router } from "@angular/router";

@Component({
    selector: 'teacher-panel',
    templateUrl: './teacher.component.html',
    styleUrls: ['./teacher.component.css']
})
export class TeacherComponent {
    constructor(public teacherStore: TeacherStore,
                private router: Router,
                private route: ActivatedRoute
    ) {
    }

    navigateToProject(project: string): void {
        this.router.navigate(["project", project], {relativeTo: this.route});
    }

    navigateToClass(clazz: string): void {
        this.router.navigate(["class", clazz], {relativeTo: this.route})
    }
}