import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { TeacherClassStore } from "./teacher-class.store";

@Component({
    selector: 'teacher-class-panel',
    templateUrl: './teacher-class.component.html',
    styleUrls: ['./teacher-class.component.css']
})
export class TeacherClassComponent implements OnInit {
    classId: string;

    constructor(public store: TeacherClassStore, private route: ActivatedRoute) {
    }

    ngOnInit(): void {
        this.classId = this.route.snapshot.paramMap.get("id");
    }
}