import { Injectable } from "@angular/core";
import { action, observable } from "mobx";

interface TeacherClassState {
    students: string[];
    lessons: string[];
}

@Injectable()
export class TeacherClassStore {
    @observable
    state!: TeacherClassState;

    constructor() {
        this.initState();
    }

    @action
    initState(): void {
        this.state = {
            students: [
                "Иванов",
                "Петов",
                "Сидоров"
            ],
            lessons: [
                "Обучение QForm литье",
                "Обучение QForm штамповка",
                "Обучение QForm основы"
            ]
        };
    }
}